@import url('https://fonts.googleapis.com/css2?family=Oswald&family=Tilt+Warp&display=swap');

* {
    margin: 0;
    padding: 0;
    border : 0;
    outline: 0;
    box-sizing: border-box;
    list-style:none;
    text-decoration: none;
}

/*we can use root anyone in the project*/
:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-varient: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;  /* for all the container classes*/
    --container-width-md: 86%;  /*lg is large, md is medium and sm is small*/
    --container-width-sm: 95%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display:none;
}

body{
    font-family: 'Tilt Warp', cursive;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg.jpg);
    background-image: center;
    background-size: cover;
}

/*container class*/
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 { font-weight: 500;}

h1 { font-size: 2.5rem; }

h6 { font-size: 1.75rem; }

section{
    margin-top: 8rem;
    height: 90vh;
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
}

.text-light{
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

/*button*/
.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-white);
}


/*the picture button class below is not used nor declared*/

.btn-picture{
    width: 15rem;
    height: 15rem;
    cursor: pointer;
    background:transparent;
    border: none;
}


img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES FOR MEDIUM DEVICES*/

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}


@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }

    section{
        margin-top: 2rem;
    }
}